





















































/* BLOT : style */
.main-header{
     background-color:white;
}
.main-header .logo {
        background:none;
}
.main-header .logo {
        font-family: 'Noto Sans KR', sans-serif;
        width:278px;
        text-align:left;
        padding-left:15px;
}
.logo i {
        color: #6B6B6B;
        font-weight:100;
        font-size:20px;
        padding-right:20px;
}
.logo span {
        color: #6B6B6B;
        height:30px;
        vertical-align:top;
        font-size:20px;
        font-weight:600;
}
.main-header>.navbar{
        margin-left:278px;
}
.search-logo {
        font-size:20px;
        vertical-align: middle;
        display:inline;
}
.search-value {
        display:inline;
        line-height: 0px;
}



